<template>
    <div>
        <div style="height: 80px">
            <detail-header>
                <my-nav :scroll="true"></my-nav>
            </detail-header>
        </div>
        <div class="topic-main">
            <div class="topic-detail">
                <div class="detail" v-for="(item, index) in showData" :key="item.id">
                    <span>{{index + 1}}楼: </span>
                    <div v-html="item.text"></div>
                </div>

            </div>
            <div class="topic-publish">
                <p>来个匿名吐槽，发泄一下你心中的怒火吧！</p>
                <el-button type="primary" @click="changeDialog">发吐槽</el-button>
            </div>
        </div>

        <publish-topic :dialog-table-visible.sync="dialogTableVisible"
                       v-if="dialogTableVisible"
                       :detail-list.sync="detailList"></publish-topic>

    </div>
</template>

<script>
    import DetailHeader from "../components/DetailHeader";
    import MyNav from "../components/MyNav";
    import publishTopic from "./publishTopic"
    import {getTopic} from '@/service/info-api'

    export default {
        name: "Topic",
        created() {
            getTopic()
            .then((res) => {
                if (res.code === 2000){
                    this.detailList = res.topic_list
                }
            })
        },
        computed: {
            showData: {
                get: function() {
                    return this.detailList
                },
                set: function (newValue) {
                    this.detailList = newValue
                }
            }
        },
        components: {
            DetailHeader,
            MyNav,
            publishTopic
        },
        data(){
            return {
                dialogTableVisible: false,
                detailList: []
            }
        },
        methods: {
            changeDialog(){
                this.dialogTableVisible = true
            }
        }
    }
</script>

<style lang="scss">
    .topic-main{
        display: flex;
        flex-direction: row;
        margin-top: 50px;
        justify-content: center;
        .topic-detail{
            width: 1000px;
            height: 500px;
            background: #ffffff;
            .detail{
                margin: 10px 40px;
                border: 1px solid black;
                border-radius: 10px;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
            }
        }
        .topic-publish{
            width: 300px;
            height: 100px;
            margin-left: 20px;
            background: #ffffff;
            border-radius: 10px;
            border: 1px solid blue;
            font-size: 14px;
            .el-button{
                margin-top: 5px;
                width: 260px;
                height: 30px;
                font-size: 14px;
                text-align: center;
                line-height: 0;
            }
        }
    }

</style>
